<template>
	<div class="info-form">
		<Form ref="formData" :model="formValidate" :rules="ruleValidate" :label-width="80">
			<FormItem label="密码" prop="password">
				<i-input type="password" v-model="formValidate.password" clearable size="large"
				         placeholder="请输入你的密码"></i-input>
			</FormItem>
			<FormItem label="确认密码" prop="rePassword">
				<i-input type="password" v-model="formValidate.rePassword" clearable size="large"
				         placeholder="请再次输入你的密码"></i-input>
			</FormItem>
			<Button type="error" size="large" long @click="handleSubmit">{{strings}}</Button>
		</Form>
	</div>
</template>
<script>
import {mapMutations} from 'vuex';

export default {
  name:"InputInfo",
  data(){
    const validatePassCheck = (rule, value, callback) => {
				if (value !== this.formValidate.password) {
					callback(new Error('两次输入的密码不一样'));
				} else {
					callback();
				}
			};
    return{
        formValidate: {
					password: '',
					rePassword: '',
				},
				ruleValidate: {
					password: [
						{required: true, message: '密码不能为空', trigger: 'blur'},
						{type: 'string', min: 6, message: '密码长度不能小于6', trigger: 'blur'}
					],
					rePassword: [
						{required: true, message: '请再次输入密码', trigger: 'blur'},
						{validator: validatePassCheck, trigger: 'blur'}
					]
        },strings:"注册",
        Status:"",
    }
  },
  created(){
      this.Status=sessionStorage.getItem("regStatus");
      if(this.Status==1){
        this.strings="找回密码"
      }
  },
  mounted(){
    this.SET_SIGN_UP_STEP(1);
  },methods:{
    ...mapMutations(['SET_SIGN_UP_STEP']),
    handleSubmit(){
      this.$refs['formData'].validate(async (valid)=>{
        if(valid){
          let res='';
          if(this.Status==1){
            res= await this.$request.RetrievePassword({LoginName:this.$route.query.phone,PWD:this.formValidate.password});
          }else{
            res= await this.$request.regist(this.$route.query.phone,this.formValidate.password);
          }
          if(res.code==0){
            this.$Message.success('操作成功');
						this.SET_SIGN_UP_STEP(2);
            this.$router.push('/register/SignUpDone');
            localStorage.removeItem('supliepwd');
          }else if(res.code==1){
							this.$Message.error(res.msg);
						}
        }else {
						this.$Message.error('请填写正确的信息');
					}

      })
    }
  }
}
</script>
<style scoped>
	.info-form {
		width: 90% !important;
	}
</style>



